import * as THREE from 'three';
import { gsap } from 'gsap';

export function setupCamera(camera, scene, controls, renderer) {
    /**
     * Variables d'tat
     */
    let scrollEnabled = false;
    let scrollProgress = 0;
    let finalAnimationTriggered = false;
    let isMuted = false; // tat du son (dsactiv par dfaut)

    /**
     * Points de la trajectoire
     */
    const camStart = new THREE.Vector3(0, 1, 8);  // dpart
    const camEnd = new THREE.Vector3(0, 2, -39);   // arrive

    /**
     * Gestion du scroll
     */
    function updateScrollProgress() {
        const max = document.documentElement.scrollHeight - window.innerHeight;
        if (max > 0) {
            scrollProgress = Math.min(window.scrollY / max, 1);
        }
    }
    window.addEventListener('scroll', updateScrollProgress, { passive: true });

    /**
     * Configuration audio avec filtre lowpass
     */
    let audioContext;
    let audioSource;
    let filter;
    let gainNode;

    /**
     * Bouton d'entre et bouton de son
     */
    const overlay = document.querySelector('.overlay');
    const button = document.querySelector('#enter');
    const soundToggleButton = document.querySelector('#toggle-sound');
    const clickaudio = new Audio('./sounds/musique.mp3');

    // Fonction pour basculer le son
    function toggleSound() {
        if (!audioContext) return;

        isMuted = !isMuted;

        if (isMuted) {
            gainNode.gain.value = 0;
            soundToggleButton.classList.add('muted');

            // Animation smooth pour redescendre les barres
            const waves = document.querySelectorAll('.wave');
            waves.forEach((wave, index) => {
                gsap.to(wave, {
                    height: '2px',
                    duration: 0.5,
                    delay: index * 0.1, // Dcalage progressif pour chaque barre
                    ease: "power2.inOut"
                });
            });
        } else {
            gainNode.gain.value = 0.1 + (0.9 * scrollProgress);
            soundToggleButton.classList.remove('muted');

            // Animation pour faire remonter les barres en vague
            const waves = document.querySelectorAll('.wave');
            waves.forEach((wave, index) => {
                gsap.to(wave, {
                    height: '100%',
                    duration: 0.8,
                    delay: index * 0.1,
                    ease: "elastic.out(1, 0.5)", // Effet "rebond" pour un retour dynamique
                    onComplete: () => {
                        // Redmarre l'animation sinusodale aprs la remonte
                        animateWaves();
                    }
                });
            });
        }
    }

    // couteur pour le bouton de son
    if (soundToggleButton) {
        soundToggleButton.addEventListener('click', (e) => {
            e.stopPropagation(); // Empche le dclenchement d'autres vnements
            toggleSound();
        });
    }

    function animateWaves() {
        if (isMuted) return; // Ne pas animer si le son est dsactiv

        const waves = document.querySelectorAll('.wave');
        const now = Date.now();
        const frequency = 0.002; // Contrle la vitesse de l'onde

        waves.forEach((wave, index) => {
            // Utilise sin() pour crer une courbe lisse
            const offset = index * 0.5; // Dcale chaque barre
            const height = 5 + 15 * Math.sin(now * frequency + offset); // Hauteur entre 5% et 20%
            wave.style.height = `${height}px`;
            wave.style.transform = `scaleY(${height / 20})`; // Ajuste l'chelle
        });

        requestAnimationFrame(animateWaves); // Boucle d'animation
    }

    button.addEventListener('click', () => {
        // Place la camra au dpart
        camera.position.copy(camStart);
        camera.lookAt(0, 2, 0);
        scrollEnabled = true;
        controls.enabled = false;  // Dsactive les contrles pendant le scroll

        // Animation de l'overlay
        overlay.style.opacity = 0;
        setTimeout(() => {
            overlay.style.display = 'none';

            // Afficher les lments
            const soundToggleButton = document.querySelector('#toggle-sound');
            const musicCredit = document.querySelector('.music-credit');
            if (soundToggleButton) soundToggleButton.style.display = 'flex';
            if (musicCredit) musicCredit.style.display = 'block';

            // Animer l'apparition
            gsap.from([soundToggleButton, musicCredit], {
                opacity: 0,
                y: 20,
                duration: 0.5,
                stagger: 0.1, // Dcalage entre les animations
                ease: "power2.out"
            });
        }, 600);

        // Initialisation de l'AudioContext et du filtre
        audioContext = new (window.AudioContext || window.webkitAudioContext)();
        audioSource = audioContext.createMediaElementSource(clickaudio);
        filter = audioContext.createBiquadFilter();
        gainNode = audioContext.createGain();

        // Configuration du filtre (lowpass fort au dbut)
        filter.type = "lowpass";
        filter.frequency.value = 200;
        filter.Q.value = 0.5;

        audioSource.connect(filter);
        filter.connect(gainNode);
        gainNode.connect(audioContext.destination);

        clickaudio.currentTime = 0;
        clickaudio.play().catch(e => console.log("La lecture a chou :", e));

        animateWaves();
    });

    /**
     * Fonction de mise  jour de la camra et du filtre audio
     */
    function updateCamera() {
        if (scrollEnabled && !finalAnimationTriggered) {
            // Interpolation linaire entre camStart et camEnd
            camera.position.lerpVectors(camStart, camEnd, scrollProgress);

            if (!isMuted) {
                gainNode.gain.value = 0.1 + (0.9 * scrollProgress);
            }

            if (filter) {
                const minFreq = 200;      // Frquence minimale
                const maxFreq = 22050;    // Frquence maximale
                filter.frequency.value = minFreq + (maxFreq - minFreq) * scrollProgress;
            }

            // Dtecte si on atteint la fin du scroll
            if (scrollProgress >= 1 && !finalAnimationTriggered) {
                finalAnimationTriggered = true;
                scrollEnabled = false; // Dsactive le scroll pendant l'animation

                // Animation de retour avec GSAP
                const timeline = gsap.timeline();

                // Animation de la camra
                timeline.to(camera.position, {
                    x: camStart.x,
                    y: camStart.y,
                    z: camStart.z,
                    duration: 3,
                    ease: "power2.inOut"
                }, 0);

                // Animation du volume (gain)
                if (!isMuted) {
                    timeline.to(gainNode.gain, {
                        value: 0.1, // Retour au volume initial
                        duration: 3,
                        ease: "power2.inOut"
                    }, 0);
                }

                // Animation du filtre (lowpass)
                timeline.to(filter.frequency, {
                    value: 200, // Retour  la frquence initiale
                    duration: 3,
                    ease: "power2.inOut",
                    onComplete: () => {
                        // Rinitialise le scroll de la page  0%
                        window.scrollTo({ top: 0, behavior: 'instant' });
                        // Rinitialise les variables pour permettre un nouveau scroll
                        scrollProgress = 0;
                        finalAnimationTriggered = false;
                        scrollEnabled = true;
                        // Ractive les contrles si ncessaire
                        controls.enabled = false;
                    }
                }, 0);
            }
        }
    }

    return {
        scrollEnabled: () => scrollEnabled,
        finalAnimationTriggered: () => finalAnimationTriggered,
        camStart,
        camEnd,
        updateCamera,
    };
}
